:global body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Oxygen-Sans, "Fira Sans", "Droid Sans", Ubuntu, Cantarell, "Helvetica Neue", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}
:global .clear {
  clear: both;
}
:global a:link {
  -webkit-tap-highlight-color: #e3fcff;
}
:global html,
:global body,
:global #wrapper,
:global #root {
  min-height: 100%;
  _height: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
:global .no-js body > * {
  display: none !important;
}
:global .no-js .no-js {
  display: block !important;
  background: url("../img/js.jpg") 0 0 no-repeat;
  background-size: cover;
}
:global ::-webkit-input-placeholder {
  font-size: 12px;
}
:global :-ms-input-placeholder {
  font-size: 12px;
}
:global :-moz-placeholder {
  font-size: 12px;
}
:global ::-moz-placeholder {
  font-size: 12px;
}



